@font-face {
  font-family: 'Antiqua';
  src: url("../fonts/Antiqua.eot?#iefix") format("embedded-opentype"), url("../fonts/Antiqua.woff") format("woff"), url("../fonts/Antiqua.ttf") format("truetype"), url("../fonts/Antiqua.svg#Antiqua") format("svg");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Oswald';
  src: url("../fonts/Oswald-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Oswald-Regular.woff") format("woff"), url("../fonts/Oswald-Regular.ttf") format("truetype"), url("../fonts/Oswald-Regular.svg#Oswald-Regular") format("svg");
  font-weight: normal;
  font-style: normal;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.sta {
  max-width: 720px;
  width: 90%;
  margin: 0 auto;
  padding: 230px 0 50px;
  outline: 1px solid red;
}

.sta:after {
  content: " ";
  display: table;
  clear: both;
}

.sta__image {
  width: 307px;
  height: 291px;
  float: left;
}

.sta__image img {
  width: 100%;
  height: 100%;
}

.sta__content {
  float: right;
  padding-top: 55px;
}

.sta__only {
  display: inline-block;
  font-weight: 700;
  color: #d24a43;
  font-size: 48px;
  margin-bottom: 17px;
  font-family: Antiqua;
  line-height: 0.5em;
  margin-bottom: 27px;
}

.sta__text {
  font-weight: bold;
  color: #626262;
  font-size: 33px;
  margin-bottom: -10px;
  font-family: Antiqua;
  font-weight: 400;
  font-variant: small-caps;
  line-height: 0.5em;
  margin-bottom: 20px;
}

.sta__text b {
  font-weight: 700;
  font-size: 50px;
  color: #D24A43;
  font-family: Antiqua;
}

.sta__gift {
  color: #d86a43;
  font-family: Oswald;
  font-size: 87px;
  font-weight: 600;
  position: relative;
  line-height: 0.7em;
}

.sta__gift:before {
  display: block;
  content: '';
  background: url("../img/arrow_sta.png") 0 0 no-repeat;
  width: 117px;
  height: 81px;
  -webkit-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  position: absolute;
  left: 0;
  margin-left: -122px;
  opacity: 0.7;
}

.sta__gift:after {
  display: block;
  content: '';
  width: 160px;
  height: 330px;
  background: url("../img/ball.png") no-repeat;
  position: absolute;
  background-size: 100% 100%;
  right: -72px;
  top: -331px;
}
